.events-landing{
margin:23px 0px 20px 0px;
padding-top:30px;
.page-landing-header{
  img:nth-child(1){
    border-right:2px solid $toro-red;
  }
  img:nth-child(3){
    border-left:2px solid $toro-red;
  }
}
}
.events-description{
  margin:10px 0px 40px 0px;

  h3{
      font-family: $domaine;	font-size: 34px;	font-weight: bold;	line-height: 50px;
  }
  h4{
      font-family: $domaine;	font-size: 25px;	font-weight: bold;	line-height: 28px;color: $toro-red;letter-spacing: 0px;
  }
}

.mobile-happenings-title{
  display: none;
}
.filter{
  display: flex;
  margin:20px 0px;
  justify-content:space-between;
  flex-wrap: wrap;

  align-items: flex-start;
button{
  width:14%;
  height:65px;
  color:$toro-red;
  text-transform: uppercase;
  border:2px solid $toro-red;
  background-color:white;
  font-family: $knockout-lite;
  font-size:2rem;
  line-height: 1rem;
cursor: pointer;

}
button.active{
  background-color:$toro-red;
  color:white;
}
h1{
}
h2{
  font-family:$knockout-feather;
  text-transform: uppercase;
  color:$toro-red;
  font-size:6rem;
  line-height: .6em;
  margin-right:10px;
}


}


.all-events{
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 30px;
    transition: 2s height ;
    transition-timing-function: cubic-bezier(0.25, 0.1, 0.09, 0.99);
      min-height:535px;
    .home-happenings-event{
      width: 24%;
      padding: 10px;
      margin:10px 5px;
      border: 2px solid $toro_red;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      text-decoration: none;
      transition: ease 250ms all;


      .home-happenings-upper{
        margin-bottom: 20px;
      }
      h6{
        font-family: $domaine;
        font-weight: 900;
        margin-bottom: 10px;
      }
      h3{
        text-transform: uppercase;
        color: $toro_red;
        font-family: $knockout-feather;
        font-size: 40px;
        line-height: 1em;
        margin-bottom: 7px;
      }
      .home-happenings-img{
        width: 100%;
        height: 0;
        padding-bottom: 100%;
        position: relative;
      }
      img{
        position: absolute;
        left: 0;
        top:0;

        object-fit: cover;
        width: 100%;
        height: 100%;
      }

      h3, h6, p{
        transition: ease 250ms all;
      }

      &:hover{
        background-color: $toro_red;
        h3, h6, p{
          color: white;
        }
      }

    }
  }


@media screen and (max-width: $mobile_max) {
  .mobile-happenings-title{
    display: block;
    color: $mid_grey;
    text-transform: uppercase;
    text-align: center;
  }
  .events-landing .page-landing-header{
    display: none;
  }
  .events-description{
  h3{
    font-size:2.7rem !important;
    line-height: 1.7em;
    margin:20px 0px;
  }
}
.all-events{
  .home-happenings-event{
    width: 100%;
  }
}
.filter{

  h2{
    text-align: center;
    font-size:4rem;
    width:100%;
    margin-bottom:15px;
  }
  button{
    width:48%;
    padding:1%;
    margin:1%;

  }
}

}



